@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
    margin:0;
    padding:0;
    border:0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#000;
    --color-bg-variant:#393E46;
    --color-primary:#EAC435;
    --color-primary-variant:rgba(239, 91, 12,.4);
    --color-secondary:#F15412;
    --color-white:#fff;
    --color-light:rgba(255,255,255,.6);

    --transition:all 400ms ease;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
}
#root{
    position:relative;
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display:none;
}
body{
    background: var(--color-bg);
    color: var(--color-white);
    line-height:1.7;
    font-family: 'Poppins', sans-serif;
    background-color:#301934;
    font-size:1.2rem;
}

/* ----------- STYLES ------------- */

.container {
    width:var(--container-width-lg);
    margin:0 auto;
}
h1, h2, h3, h4, h5{
    font-weight:500;
}
h1{
    font-size:2.5rem;
}

section{
    margin-top:8rem;
    min-height:60vh;
}
section > h2, section > h5{
    text-align:center;
    color:var(--color-light);
}
section > h2{
    color:var(--color-primary);
    margin-bottom:3rem;
}
.text-light{
    color:var(--color-light);
}
a{
    color:var(--color-primary);
    transition:var(--transition);
}
a:hover{
    color:var(--color-white);
}
.btn{
    width:max-content;
    display:inline-block;
    color:var(--color-primary);
    padding:.75rem 1.2rem;
    border-radius:.4rem;
    cursor:pointer;
    border:1px solid var(--color-primary);
    transition:var(--transition);
}
.btn:hover{
    background:var(--color-white);
    color:var(--color-bg);
    border-color:transparent;
}
.btn-primary{
    background:var(--color-primary);
    color:var(--color-bg);
}
img{
    display:block;
    width:100%;
    object-fit: cover;
}

/* ----------- TAGS ------------ */
.top-tags {
    bottom: auto;
    top: 2rem;
}


/* ----------- MEDIA QUERIES (MD DEV) ------------- */
@media screen and (max-width:1024px){
    .container{
        width:var(--container-width-md);
    }
    section{
        margin-top:6rem;
    }
}
/* ----------- MEDIA QUERIES (SM DEV) ------------- */
@media screen and (max-width:600px){
    .container{
        width:var(--container-width-sm);
    }
    section > h2{
        margin-bottom:2rem;
    }
}