.services__container{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    gap:3rem;
}
.service{
    background:var(--color-bg-variant);
    border-radius:0 0 2rem 2rem;
    border:1px solid var(--color-primary);
    height:fit-content;
    transition:var(--transition);
}
.service:hover{
    background:transparent;
    border-color:var(--color-primary-variant);
}
.service__head{
    background:var(--color-primary);
    padding:2rem;
    border-radius:0 0 2rem 2rem;
    box-shadow:0 2rem 1rem rgba(0,0,0,.1);
}
.service__head h3{
    color:var(--color-bg);
    font-size:1.2rem;
    text-align:center;
}
.service__list{
    padding:2rem;
}
.service__list li{
    display:flex;
    gap:1rem;
    margin-bottom:.8rem;
}
.service__list-icon{
    color:var(--color-primary);
    margin:2px;
}
.service__list p{
    font-size:1rem;
}

/* ----------- MEDIA QUERIES (MD DEV) ------------- */
@media screen and (max-width:1024px){
   .services__container{
        grid-template-columns: 1fr 1fr;
        gap:2rem;
   }
   .service{
    height:auto;
   }
}
/* ----------- MEDIA QUERIES (SM DEV) ------------- */
@media screen and (max-width:600px){
    .services__container{
        grid-template-columns: 1fr;
        gap:1.5rem;
   }
}