nav{
    background:rgba(0,0,0,.3);
    width:max-content;
    display:block;
    padding:.7rem 1.7rem;
    z-index:2;
    position:fixed;
    left:50%;
    transform:translateX(-50%);
    bottom:2rem;
    display:flex;
    gap:.8rem;
    border-radius:3rem;
    backdrop-filter: blur(15px);
}
.cta-btn{
    height:35px;
    display:flex;
    align-items:center;
}
nav a{
    padding:.9rem;
    background:transparent;
    border-radius:50%;
    display:flex;
    color:var(--color-light);
    font-size:1.2rem;
}
nav a:hover{
    background:rgba(0,0,0,.3);
}
nav a.active {
    background:var(--color-primary);
    color:var(--color-);
}